@font-face {
    font-family: 'BookkMyungjo-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/BookkMyungjo-Bd.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'bagnard';
    src: url('../fonts/bagnard.regular.otf');
}
.book-top{
    margin: 0 auto;
}
.header{
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
   
}
.header-content{
    background-color: rgba(255,255,255,0.8);
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.title{
    font-family: 'bagnard';
    color: rgb(84, 93, 150);

}

.search-form{
    margin-top: 20px;
    display: flex;
    justify-content: center;

}
.search-form>button{
    background: none;
    border: none;
}
.search{
    height: 30px;
    width: 40%;
    justify-content: center;
     border: none;
    border-bottom: 1px solid #ccc;
}

.cover{
    margin-top: 71px;
    width: 100%;
    height: 40vh;
    overflow: hidden;
    position: relative;
    background-color: rgb(115, 120, 152);
}
.img-txt{
 
    width: 520px;
    font-family: 'BookkMyungjo-Bd';
    position: absolute;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    font-size: 28px;
    margin-left: -265px;
    margin-top: -12px;
    color: #fff;
}
.img-txt>p{
    font-size: 18px;
    text-align: center;
}
.cover-img{
    width: 100%;
    height: auto;
    opacity: 0.3;


}